import React, { useEffect, useState } from 'react';
import Loading from '../components/Loading';
import Container from '../components/Container';
import axios from 'axios';
import { Redirect } from 'react-router-dom';

const list = {
  songList: [
    {
      id: 1,
      title: 'Where Are We Now',
      artist: '문별',
      lyrics: '어디론가 날 데려가 줄래 아님 저 바람 부는 대로',
    },
    {
      id: 2,
      title: 'HIP',
      artist: '문별',
      lyrics: '날 자극한 여러분 감사 거기서 멈춘 찌질이 반사 덕분에 나의 멘탈은 단단해',
    },
    {
      id: 3,
      title: '데칼코마니',
      artist: '휘인',
      lyrics: '여잔 촉이 정말 빠르지 이미 물은 엎질러졌어 우린 몰래 새벽을 맞네',
    },
    {
      id: 4,
      title: '별이 빛나는 밤',
      artist: '휘인',
      lyrics: '겨울이 지나고 또다시 꽃은 피고 따뜻한 공기 냄새가 날 감싸',
    },
    {
      id: 5,
      title: '딩가딩가',
      artist: '솔라',
      lyrics: '왜 다들 화나 있어 요즘 너무 삭막해 그저 달콤한 휴식이 필요해',
    },
    {
      id: 6,
      title: '음오아예',
      artist: '화사',
      lyrics: '시간 좀 되나요 나 좀 도와줘요 그 쪽 때문에 오감이 제멋대로예요',
    },
    {
      id: 7,
      title: '넌 is 뭔들',
      artist: '솔라',
      lyrics: '센스매너 말투표정 행동 하나까지 섬세한 널 알아보는 나',
    },
    {
      id: 8,
      title: '나로 말할 것 같으면',
      artist: '화사',
      lyrics: '겨울에 민소매 입으면 여름이 되는 거야 I love Ya Introduce My Self',
    },
    {
      id: 9,
      title: '너나 해',
      artist: '휘인, 솔라',
      lyrics: '밤을 새워 버렸어 차갑게 식은 머리도 너의 태도가 이해 안 돼 이젠 조금씩 지쳐 가네',
    },
    {
      id: 10,
      title: 'AYA',
      artist: '문별',
      lyrics: '님 얼굴에 침을 뱉어 오점 하나 생겨 우린 남이 됐다',
    },
  ],
};

function App() {
  const [songs, setSongs] = useState([]);
  const [count, setCount] = useState(0);
  const [lyrics, setLyrics] = useState([]);
  const [view] = useState([]);
  const [change, setChange] = useState('');
  const [result, setResult] = useState([]);
  const [wrongAnswer, setWrongAnser] = useState(0);
  const [rightAnswer, setRightgAnser] = useState(0);
  const [isLoding, setIsLoding] = useState(true);
  const [seconds, setSeconds] = useState(15);

  // 셔플하기
  const shuffleBox = () => {
    // 타이틀 바꾸기
    setLyrics([]);
    songs.map((v) => lyrics.push(v.lyrics));
    setChange(lyrics[count]);

    // 보기 섞기
    const shuffle = [];
    const shuffle2 = [];
    songs.map((v) => view.push(v.title));
    const wow = view.splice(count, 1);
    for (let i = 0; i < 9; i++) {
      shuffle.push(view.splice(Math.floor(Math.random() * view.length), 1)[0]);
    }
    shuffle.unshift(wow);
    const noNmae = shuffle.slice(0, 4);
    for (let i = 0; i < 4; i++) {
      shuffle2.push(noNmae.splice(Math.floor(Math.random() * noNmae.length), 1)[0]);
    }
    setResult(shuffle2);
  };

  // 첫 시작값
  useEffect(() => {
    setSongs(list.songList);
    setIsLoding(false);
    shuffleBox();
    return shuffleBox();
  }, [isLoding, count]);

  const inTitle = (e) => {
    // 카운터 늘리기
    setCount(count + 1);
    if (count === 10) {
      return setCount(10);
    }

    setSeconds(15);

    shuffleBox();
    // 맞는지 틀린지 판단
    if (e.target.textContent === songs[count].title) {
      setRightgAnser(rightAnswer + 1);
    } else {
      setWrongAnser(wrongAnswer + 1);
    }
  };

  // 타이머
  useEffect(() => {
    const countdown = setInterval(() => {
      if (parseInt(seconds) > 0) {
        setSeconds(parseInt(seconds) - 1);
      }
      if (parseInt(seconds) === 0) {
        setCount(count + 1);
        setWrongAnser(wrongAnswer + 1);
        setSeconds(15);
      }
    }, 1000);
    return () => clearInterval(countdown);
  }, [seconds, count, wrongAnswer]);

  // 카운터가 10개 넘으면은 결과창으로
  if (count === 10) {
    if (rightAnswer <= 2) {
      return <Redirect to="/result1" />;
    } else if (rightAnswer <= 5) {
      return <Redirect to="/result2" />;
    } else if (rightAnswer <= 8) {
      return <Redirect to="/result3" />;
    } else {
      return <Redirect to="/result4" />;
    }
  }

  return (
    <>
      {isLoding ? (
        <div>
          <Loading />
        </div>
      ) : (
        <Container>
          <h2>{seconds < 10 ? `0${seconds}` : seconds}</h2>
          <h1>{change}</h1>
          <div>{count + 1}/10</div>
          <button onClick={inTitle}>{result[0]}</button>
          <button onClick={inTitle}>{result[1]}</button>
          <button onClick={inTitle}>{result[2]}</button>
          <button onClick={inTitle}>{result[3]}</button>
          {rightAnswer}:{wrongAnswer}
        </Container>
      )}
    </>
  );
}

export default App;
